import React from 'react'
import { Page, PageCover, EventTile, Separator, TextBlock } from '../components'

const SubPage = ({ id, parentData }) => {
	const data = parentData.sub_pages.find(p => p.id === id)
	return (
		<Page>
			<PageCover data={data} />
			<Separator />
			<TextBlock data={data.text_block} />
			{data.events && data.events.map(
				(event, i) => (
					<EventTile key={i} n={i} data={event} />
				)
			)}
		</Page>
	)
}


export default SubPage