import React from 'react'
import { Page, PageCover, EventTile, Separator } from '../components'

const Projects = ({ data }) => (
	<Page>
		<PageCover data={data} />
		<Separator />
		{data.events && data.events.map(
			(event, i) => (
				<EventTile key={i} n={i} data={event} />
			)
		)}
		{data.sub_pages && data.sub_pages.map(
			(page, i) => (
				<EventTile key={i} n={i} data={page} parentPage={data.id} />
			)
		)}
	</Page>
)


export default Projects